:root {
  --darkblue: rgb(55, 46, 128);
  --lightblue: rgb(69, 167, 212);
  --orange: rgb(214, 137, 50);
  --white: rgb(250, 250, 250);
}

html,
body {
  font-size: 10px;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  background: var(--white)
}

.loading-spinner {
  border:10px solid rgb(190, 190, 190);
  border-top: 10px solid var(--darkblue);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin: 100px auto;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { 
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.header {
  border: 1px solid black;
  padding: 5px;
  background: var(--darkblue);
  display: flex;
  justify-content: flex-start;
}

form {
  text-align: center;
  padding: 15px;
  margin: 0 auto;
}

form > * {
  padding: 5px;
  background: var(--white);
  border: none;
}

form input {
  border-radius: 3px;
}

form button {
  border-radius: 5px;
}

.current {
  margin: 20px;
  text-align: center;
  font-size: 1.8rem;
}

.details-container {
  font-size: 1.1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  background: var(--darkblue);
  color: var(--white);
  padding-top: 20px;
}

.grid {
  text-align: center;
  padding: 10px;
}

@media (min-width: 980px) {
  .details-container {
    font-size: 1.5rem;
    position: relative;
    background: var(--white);
    color: var(--darkblue);
    grid-template-columns: repeat(3, 1fr);
    max-width: 1575px;
    margin: 0 auto;
  }
}